import React from 'react';
import Link from 'next/link';
import { Calendar, Compass } from 'lucide-react';
import { Button } from '@/components/ui/button';

export const NoTripView = () => {
    return (
        <div className="flex flex-col items-center justify-center py-12 px-4 text-center space-y-4">
            <div className="h-16 w-16 rounded-full bg-slate-100 flex items-center justify-center">
                <Compass className="h-8 w-8 text-slate-400" />
            </div>

            <div className="space-y-2 max-w-md">
                <h3 className="text-lg font-semibold text-foreground">No active trips</h3>
                <p className="text-sm text-muted-foreground">
                    You don't have any upcoming bookings. Ready to plan your next adventure?
                </p>
            </div>

            <Link href="/experiences">
                <Button>
                    Explore Experiences
                </Button>
            </Link>
        </div>
    );
};
